.homepage-v12 {
  body {
    background-color: #f2f2f2;
  }
  .btn {
    white-space: nowrap;
     @include media-breakpoint-down(sm) {
        margin: auto;
     }
  }
  .angle-right {
      width: 1.5em;
      height: 1.5em;
      padding-bottom: 2px;
      fill: #00c268;
  }
  .bg-f7 {
    background-color: #f7f7f7;
  }
  
  .hero-static {
    position: relative;
    overflow: hidden;
    background-color: #fff;
    h1 {
      line-height: 55px;
    }
    h3 {
      font-size: 22px;
      line-height: 32px;
      width: 78%;
    }
  }
  .hero-title {
    width: 90%;
    .display-4 {
      font-size: 60px;
      line-height: 66px;
    }
    @include media-breakpoint-between(xs, lg) {
       .display-4 {
        font-size: 28px;
        line-height: 37px;
      }
      h4 {
        font-size: 1.0rem;
      }
    }
  }
  .hero-image {
    margin-top: 50px;
    .customer-quote {
      z-index: 3;
      position: absolute;
      top: 15px;
      color: $white;
    }
    .customer-info {
      z-index: 3;
      position: absolute;
      bottom: 140px;
      left: 30px;
      line-height: 10px !important;
    } 
    .bg {
      background-color: $primary;
      left: 0;
      top: 0;
      bottom: 0;
      position: absolute;
      width: 70vw;
      height: 420px;
      z-index: 0;
    }
    .gina {
      img {
        margin-top: 20px;
        margin-left: 280px;
        z-index: 1;
        position: relative;
      }
    }
  }

  .cta-sticky {
    position: absolute;
    background: #fff;
    right: 40px;
    top: 20px;
    bottom: 70px;
    z-index: 1;
    flex-direction: column;
    justify-content: center;

    .cta {
      margin: auto 0;
      width: 120px;
      padding: 15px;
    }
    a {
      display: block;
      text-align: center;
      padding: 10px;
      font-size: 12px;
      color: $black;
      &:hover { color: $black; };
    }
    img {
      display: block;
      margin: auto;
      margin-bottom: 5px;
    }
    .cta-hr {
      width: 70%;
      margin: 0 auto;
    }
  }


  .digital-transformation img {
    float: right;
  }
  .digital-transformation-headings {
    color: #fff;
    margin-right: 25%;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }
  .customer-logos {
   background-color: #f7f7f7; 
  }
  .analyst-reports {
      background-color: #f7f7f7;
    .container {
      padding-top: 25px;
      a {
        color: $black;
      }
    }
    .col-md-6 {
      padding: 25px 50px 50px;
      &:first-of-type {
      border-right: 1px solid rgba(207,207,207,.4);
      }
    }
    .orange-bar:before {
      content: ' ';
      display: block;
      width: 38px;
      border-top: 5px solid $green;
      margin: 30px 0 20px 0;
    }
  }
  .spring-release {
    transition: 1s ease;
    overflow: hidden;
    padding: 50px 0;

    .container {
      position: relative;
    }

    .bg {
      background-color: #00c268;
      height: 250px;
      left: 65%;
      position: absolute;
      bottom: 74px;
      width: 30vw;
      &.blue {
        background-color: #244cde;
        height: 228px;
        left: 55%;
        position: absolute;
        width: 20vw;
        top: 50px;
      }
    }
    strong {
      color: $primary;
    }
    ol {
      list-style: none;
      padding-left: 0;
      padding-bottom: 10px;
      padding-right: 20px;
    }
    ol li {
      border-top: 1px solid #e5e5e5;
      margin-left: -3px;
    }
    ol li h4::after {
      content: " ";
      border-bottom: 3px solid #e5e5e5;
      width: 55px;
      display: block;
      margin-top: 1.5em;
    }
    ol li.active h4::after {
      border-bottom: 3px solid $green;
    }
    ol li.active {
      display: block
    }
    ol li .carousel-indicator-content {
      transition: opacity 2s ease-out;
      opacity: 0; 
      height: 0;
      overflow: hidden;
    }
    ol li.active .carousel-indicator-content {
      opacity: 1;
      height: auto;
    }
    .sr-carousel {
      margin-top: 130px;
      box-shadow: 0px 5px 80px #00000030;
    }
    .carousel-item img {
      border: 20px solid black;
      border-radius: 20px;
    }
  }
  
  .why-five9 {
    .col-md-6 {
      width: 800px;
      height: 644px;
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center;
      background-position-y: bottom;
    }
    .inner-container {
      width: 70%;
      h1 {
        font-size: 2.5rem !important;
      }
    }
    .square-1 {
      background-image: url("/assets/img/why-five9-cloud-innovation-desktop.png");
      background-color: #fff;
    }
    .square-2 {
      background-image: url("/assets/img/why-five9-practical-ai-desktop.png");
      background-color: #00C268;
    }
    .square-3 {
      background-image: url("/assets/img/why-five9-trusted-partner-desktop.png");
      background-color: #FF3465;
    }
    .square-4 {
      background-image: url("/assets/img/why-five9-integration-done-properly-desktop.png");
      background-color: #F2F2F2;
    }
  }

  @include media-breakpoint-down(lg) {
    .why-five9 {
      .col-md-6 {
        padding: 50px 25px !important;
        height: 450px;
        h1 {
          font-size: 28px !important;
          line-height: 37px;
        }
        p {
          font-size: 14px;
          line-height: 25px;
          padding: 0 !important;
        }
        .inner-container {
          width: 100%;
        }
      }
    }
  }

  .features,
  .features-mobile {
    .feature-item {
      margin-bottom: 25px;
      font-size: 16px;
      img {
        margin-right: 15px;
      }
    }
  }
  
  // Mobile / Tablet 
  @media (max-width: 1024px) {
    .hero-static {
      .hero-title {
        h1 {
          font-size: 28px;
          line-height: 37px;
        }
        h3 {
          font-size: 16px;
          line-height: 25px;
          width: 100%;
        }
      }
      .hero-image {
        img {
          margin-left: -40px;
          width: 125%;
        }
        .bg {
          width: 100vw;
        }
      }
      .cta-buttons {
        padding: 0;
        a {
          color: white;
          font-weight: 500;
        }
        .col-6 {
          padding: 20px;
          background-color: $primary;
        }
        .col-6:last-child() {
          background-color: $success;
        }
      }
    }

    .features-mobile {
      p {
        margin-bottom: 0;
      }
      h1 {
        font-size: 28px;
        line-height: 37px;
      }
      .feature-item {
        margin-bottom: 15px;
        font-size: 14px;
        img {
          margin: 0px 5px 0 0;
        }
        span {
          padding-left: 25px;
        }
      }
    }
  }
}
