
.home-alt {
  .btn {
    white-space: nowrap;
  }
  .angle-right {
    width: 1.5em;
    height: 1.5em;
    padding-bottom: 2px;
    fill: #00c268;
  }
  .bg-f7 {
    background-color: #f7f7f7;
  }

  .hero-static {
    position: relative;
    overflow: hidden;
    background-color: #fff;
    h1 {
      line-height: 55px;
    }
    h3 {
      font-size: 22px;
      line-height: 32px;
      width: 78%;
    }
  }
  .hero-title {
    width: 90%;
    .display-4 {
      font-size: 60px;
      line-height: 66px;
    }
  }
  .hero-image {
    margin-top: 50px;
    .customer-quote {
      z-index: 3;
      position: absolute;
      top: 15px;
      color: $white;
    }
    .customer-info {
      z-index: 3;
      position: absolute;
      bottom: 140px;
      left: 30px;
      line-height: 10px !important;
    } 
    .bg {
      background-color: $primary;
      left: 0;
      top: 0;
      bottom: 0;
      position: absolute;
      width: 70vw;
      height: 420px;
      z-index: 0;
    }
    .gina {
      img {
        margin-top: 20px;
        margin-left: 280px;
        z-index: 1;
        position: relative;
      }
    }
  }

  .cta-sticky {
    position: absolute;
    background: #fff;
    right: 40px;
    top: 20px;
    bottom: 70px;
    z-index: 1;
    flex-direction: column;
    justify-content: center;

    .cta {
      margin: auto 0;
      width: 120px;
      padding: 15px;
    }
    a {
      display: block;
      text-align: center;
      padding: 10px;
      font-size: 12px;
      color: $black;
      &:hover { color: $black; };
    }
    img {
      display: block;
      margin: auto;
      margin-bottom: 5px;
    }
    .cta-hr {
      width: 70%;
      margin: 0 auto;
    }
  }

  .customer-logos {
    background-color: #f7f7f7;
    img {
      filter: grayscale(1)
    }
  }

  .analyst-reports {
    background-color: #f7f7f7;
    .col-md-6 {
      padding: 25px 100px;
    }
    .col-md-6:first-of-type {
      border-right: 1px solid rgba(207,207,207,.4);
    }
    .green-bar:before {
      content: ' ';
      display: block;
      width: 38px;
      border-top: 5px solid #00C268;
      margin: 30px 0 20px 0;
    }
  }

  .why-five9 {
    .col-md-6 {
      width: 800px;
      height: 644px;
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center;
      background-position-y: bottom;
    }
    .inner-container {
      width: 70%;
      h1 {
        font-size: 2.5rem !important;
      }
    }
    .square-1 {
      background-image: url("/assets/img/why-five9-cloud-innovation-desktop.png");
      background-color: #fff;
    }
    .square-2 {
      background-image: url("/assets/img/why-five9-practical-ai-desktop.png");
      background-color: #00C268;
    }
    .square-3 {
      background-image: url("/assets/img/why-five9-trusted-partner-desktop.png");
      background-color: #FF3465;
    }
    .square-4 {
      background-image: url("/assets/img/why-five9-integration-done-properly-desktop.png");
      background-color: #F2F2F2;
    }
  }

  .features,
  .features-mobile {
    .feature-item {
      margin-bottom: 25px;
      font-size: 16px;
      img {
        margin-right: 15px;
      }
    }
  }

  // Mobile / Tablet 
  @media (max-width: 1024px) {
    .hero-static {
      .hero-title {
        h1 {
          font-size: 28px;
          line-height: 37px;
        }
        h3 {
          font-size: 16px;
          line-height: 25px;
          width: 100%;
        }
      }
      .hero-image {
        img {
          margin-left: -40px;
          width: 125%;
        }
        .bg {
          width: 100vw;
        }
      }
      .cta-buttons {
        padding: 0;
        a {
          color: white;
          font-weight: 500;
        }
        .col-6 {
          padding: 20px;
          background-color: $primary;
        }
        .col-6:last-child() {
          background-color: $success;
        }
      }
    }

    .customer-logos {
      h3 {
        font-size: 16px;
        line-height: 25px;
        width: 100%;
      }
      a {
        font-weight: 500;
      }
    }

    .analyst-reports {
      .col-md-6 {
        padding: 50px 25px;
        h1 {
          font-size: 28px;
          line-height: 37px;
        }
        p {
          font-size: 14px;
          line-height: 25px;
        }
      }
      .col-md-6:first-child {
        border-right: none;
      }
      hr {
        width: 85%;
      }
    }

    .why-five9 {
      .col-md-6 {
        padding: 50px 25px !important;
        height: 450px;
        h1 {
          font-size: 28px !important;
          line-height: 37px;
        }
        p {
          font-size: 14px;
          line-height: 25px;
          padding: 0 !important;
        }
        .inner-container {
          width: 100%;
        }
      }
    }

    .features-mobile {
      p {
        margin-bottom: 0;
      }
      h1 {
        font-size: 28px;
        line-height: 37px;
      }
      .feature-item {
        margin-bottom: 15px;
        font-size: 14px;
        img {
          margin: 0px 5px 0 0;
        }
        span {
          padding-left: 25px;
        }
      }
    }

    .customer-connections {
      .container {
        background-size: contain;
        .col-10 {
          padding: 70px;
        }
      }
    }
  }

  // Mobile Demo Button
  @media (max-width: 728px) {
    .hero-static {
      .hero-title {
        a {
          margin: -30px auto 30px auto;
        }
      }
    }
  }

}