.partners-portal {
  .btn {
    white-space: nowrap;
    font-size: 18px;
  }
  .desktop-controls {
    .carousel-control-prev,
    .carousel-control-next {
      width: 0;
    }
    .carousel-control-prev-icon {
      background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='%23717171' viewBox='0 0 8 8'%3E%3Cpath d='M5.25 0l-4 4 4 4 1.5-1.5-2.5-2.5 2.5-2.5-1.5-1.5z'/%3E%3C/svg%3E");
      left: 20px;
      position: absolute;
    }
    .carousel-control-next-icon {
        background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='%23717171' viewBox='0 0 8 8'%3E%3Cpath d='M2.75 0l-1.5 1.5 2.5 2.5-2.5 2.5 1.5 1.5 4-4-4-4z'/%3E%3C/svg%3E");
        right: 20px;
        position: absolute;
    }
  }
  .control-buttons {
    display: none;
    a {
      margin: 0 10px;
    }
    a:active {
      rect {
        fill: #e0e0e0;
      }
    }
  }
  .angle-right {
    width: 1.5em;
    height: 1.5em;
    padding-bottom: 2px;
    fill: #00c268;
  }
  .carousel-indicators > li {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    display: inline-block;
    background-color: transparent;
    border: 3px solid #00C268;
    box-sizing: border-box;
    opacity: 1;
  }
  .carousel-indicators > li:not(.active) {
    background-color: #717171;
    border: none;
  }

  .hero-static {
    position: relative;
    overflow: hidden;
    background-color: #fff;
    .hero-container {
      .hero-image-tablet {
        display: none;
      }
      .hero-title-mobile {
        display: none;
        margin-left: 9px;
        margin-right: 9px;
        .hero-image-mobile {
          margin-top: -25px;
          margin-bottom: 16px;
        }
      }
      .hero-title {
        width: 637px;
        .display-4 {
          font-size: 60px;
          line-height: 66px;
        }
        .sub-title {
          font-size: 28px;
          line-height: 37px;
          font-weight: normal;
        }
        .sub-title-mobile {
          display: none;
          font-size: 16px;
          line-height: 25px;
        }
        .summary {
          font-size: 20px;
          line-height: 34px;
        }
        .hero-image {
          margin-top: 50px;
        }
      }
    }
  }

  .customer-logos {
    background-color: #fff;
    .customer-logos-mobile,
    .customer-logos-tablet {
      display: none;
    }
    .customer-title {
      font-size: 22px;
      line-height: 26px;
    }
    .btn {
      margin-top: 55px;
    }
  }

  .headlines,
  .mobile-headlines {
    background-color: #f7f7f7;
    .container {
      padding-top: 25px;
      padding-bottom: 40px;
    }
    .carousel-inner {
      width: 90%;
    }
    .col-lg-6 {
      padding: 25px 50px 50px;
    }
    .col-lg-6:first-child {
      border-right: 1px solid rgba(207,207,207,.4);
    }
    // .col-lg-6 a {
    //   position: absolute;
    //   bottom: 20px;
    // }
    .carousel-indicators {
      bottom: -75px;
    }
  }

  .headlines {
    .col-lg-6 {
      padding: 25px 50px 50px;
      height: 280px;
      h1 {
        font-size: 2rem;
      }
      .headline-description {
        padding-top: 20px;
        position: sticky;
        top: 45%;
      }
      a {
        position: absolute;
        bottom: 0;
      }
    }
  }

  .mobile-headlines {
    display: none;
  }

  .resource-library {
    background-color: #fff;
    .carousel-inner {
      width: 95%;
    }
    .carousel-indicators {
      bottom: -30px;
    }
    .resource-title {
      font-size: 1.5rem;
      line-height: 2rem;
    }
    .resource-summary {
      font-size: 1rem;
      line-height: 1.5rem;
    }
    .subtext {
      font-size: 16px;
      line-height: 25px;
    }
    .resource-card {
      border: none;
      padding: 40px 32px;
      .card-body {
        background-color: #f7f7f7;
        img {
          height: 90px;
          width: auto;
        }
      }
      @media (min-width: 1025px) {
        height: 600px;
      }
    }
    #mobile-resources {
      display: none;
    }
    #resource-carousel {
      .resource-card {
        a {
          position: absolute;
          bottom: 75px;
        }
      }
    }
  }
  
  .event-calendar {
    background-color: #fff;
    .tba-text {
      color: #000;
      opacity: .1;
      font-size: 40px;
      line-height: 55px;
    }
  }



  .image-cards {
    background-color: #f7f7f7;
    padding-top: 120px;
    .card {
      background-color: #f7f7f7;
      margin-bottom: 20px;
    }
    .resource-title {
      font-size: 26px;
      line-height: 26px;
    }
    .resource-summary {
      font-size: 14px;
      line-height: 25px;
      padding-bottom: 20px;
    }
    .subtext {
      font-size: 14px;
      line-height: 25px;
    }
    .resource-card {
      border: none;
      padding: 40px 32px;
      .card-body {
        background-color: #fff;
      }
    }
    .blue-bar:before {
      content: ' ';
      display: block;
      width: 32px;
      border-top: 5px solid #244CDE;
      margin: 30px 0 20px 0;
    }
    .card-body img {
      margin-top: -100px;
    }
    .card-body a {
      position: absolute;
      bottom: 60px;
      margin-top: 20px;
    }
    .inner-text {
      margin: 0 15px;
    }
  }


  // Mobile
  @media (max-width: 575.98px) {
    .hero-static {
      .hero-container {
        .hero-title-mobile {
          display: block;
        }
        .hero-title {
          margin-left: 9px;
          margin-right: 9px;
          display: none;
          .display-4 {
            font-size: 28px;
            line-height: 37px;
          }
          .sub-title {
            display: none;
          }
          .sub-title-mobile {
            display: block;
          }
          .summary {
            font-size: 14px;
            line-height: 25px;
          }
        }
        .flag-group {
          display: none;
        }
      }
    }

    .customer-logos {
      .customer-logos-desktop,
      .customer-logos-tablet {
        display: none;
      }
      .customer-logos-mobile {
        display: block;
        margin-top: -40px;
      }
      .customer-title {
        font-size: 16px;
        line-height: 25px;
      }
      .customer-logos {
        background-color: #f7f7f7;
      }
    }

    .headlines {
      display: none;
    }

    .mobile-headlines {
      display: block;
      #mobile-headline-carousel {
        height: 450px;
        .carousel-indicators {
          bottom: -130px;
        }
        .col-lg-6:first-child {
          border-right: none;
        }
      }
    }

    .resource-library {
      h1 {
        font-size: 24px;
        line-height: 32px;
        padding-bottom: 20px;
        margin-top: -50px;
      }
      .card-body {
        margin: 0 -40px 0 -40px;
        img {
          width: 40px;
        }
        a {
          font-size: 16px;
          line-height: 25px;
        }
      }
      .btn {
        margin-top: 30px;
      }
      .resource-card {
        padding: 5px 20px;
      }
      #mobile-resources {
        display: block;
      }
      #resource-carousel {
        display: none;
      }
      .control-buttons {
        display: none;
      }
    }

    .event-calendar,
    .mobile-hide,
    .image-cards {
      display: none;
    }
    .control-buttons {
      display:block
    }
  }

  // Tablet Portrait
  @media (max-width: 768px) and (min-width: 576px) {
    .hero-static {
      .hero-container {
        .hero-image-tablet {
          display: block;
        }
        .hero-title {
          margin: 0 auto;
        }
        .flag-group {
          display: none;
        }
      }
    }

    .customer-logos {
      .customer-logos-desktop,
      .customer-logos-mobile {
        display: none;
      }
      .customer-logos-tablet {
        display: block;
      }

    }

    .headlines {
      .col-lg-6 {
        margin: 0 60px;
      }
      .col-lg-6:first-child {
        border-right: none;
      }
      .col-lg-6:first-child:after {
        content: "";
        display: block;
        margin: 0 auto;
        width: 100%;
        padding-top: 60px;
        border-bottom: 1px solid rgba(207,207,207,.4);
      }
      .mobile-hide {
        display: none;
      }
      display: none;
    }

    .mobile-headlines {
      display: block;
      #mobile-headline-carousel {
        height: 320px;
        .carousel-indicators {
          bottom: -130px;
        }
        .col-lg-6:first-child {
          border-right: none;
        }
      }
    }

    .resource-library {
      h1 {
        font-size: 24px;
        line-height: 32px;
        padding-bottom: 20px;
        margin-top: -50px;
      }
      .card-body {
        margin: 0 -40px 0 -40px;
        img {
          width: 40px;
        }
        a {
          font-size: 16px;
          line-height: 25px;
        }
      }
      .card-body *:not(svg) {
        padding: 0 40px;
      }
      .btn {
        margin-top: 30px;
      }
      .resource-card {
        padding: 5px 20px;
      }
      #mobile-resources {
        display: block;
        .resource-card .card-body {
          background-color: #f7f7f7;
          width: 105vw !important;
        }
      }
      #resource-carousel {
        display: none;
      }
      .control-buttons {
        display: none;
      }
    }

    .image-cards {
      display: none;
    }
    .control-buttons {
      display:block
    }
  }

  // Tablet Landscape & Small Resolution Desktop 
  @media (max-width: 1200px) and (min-width: 769px) {
    .hero-static {
      .hero-container {
        .hero-image-tablet {
          display: block;
        }
        .hero-title {
          margin: 0 auto;
        }
        .flag-group {
          display: none;
        }
      }
    }

    .customer-logos {
      .customer-logos-desktop img {
        width: 775px;
      }
    }

    .resource-library {
      h1 {
        font-size: 24px;
        line-height: 32px;
        padding-bottom: 20px;
        margin-top: -50px;
      }
      .card-body {
        margin: 0 -40px 0 -40px;
        img {
          width: 40px;
        }
        a {
          font-size: 16px;
          line-height: 25px;
        }
      }
      .card-body *:not(svg) {
        padding: 0 40px;
      }
      .btn {
        margin-top: 30px;
      }
      .resource-card {
        padding: 5px 20px;
      }
      #mobile-resources {
        display: block;
        .resource-card .card-body {
          background-color: #f7f7f7;
          width: 105vw !important;
        }
      }
      #resource-carousel {
        display: none;
      }
    }

    .image-cards {
      display: none;
      .card-body {
        width: 98%;
        img {
          margin: 0 auto;
        }
      }
    }
  }

  // Medium Resolution Desktop
  @media (min-width: 1201px) and (max-width: 1440px) {
    .flag-group {transform: scale(.8)}
  }

}
