.pricing {
  .btn {
    white-space: nowrap;
    font-size: 18px;
  }
  .angle-right {
    width: 1.5em;
    height: 1.5em;
    padding-bottom: 2px;
    fill: #00c268;
  }
  .hero-static {
    position: relative;
    overflow: hidden;
    background-color: #fff;
    .hero-container {
      .hero-title {
        width: 50%;
        .display-4 {
          font-size: 60px;
          line-height: 66px;
        }
        .summary {
          font-size: 20px;
          line-height: 34px;
        }
        .breadcrumb-item {
          font-size: 18px;
          line-height: 22px;
          font-weight: 200;
          color: #727272;
        }
        .breadcrumb-underline {
          border-bottom: 1px solid rgba(99, 99, 99, 0.3);
        }
      }
      .hero-image {
        .image-group {
          transform: scale(.9);
        }
      }
    }
  }

  .features {
    @media (min-width: 992px) {
      .col-lg-4 {
        padding: 0 30px;
      }
    }
    @media (min-width: 1200px) {
      .col-lg-4 {
        padding: 0 75px;
        h4 {
          padding: 0 20px;
        }
      }
    }
  }

  .bg-f7 {
    background-color: #F7f7f7;
  }

  .bg-f9-green {
    background-color: #00C268;
  }

  .text-black {
    color: black;
  }

  .contact-form {
    width: 85%;
    .pt-7 {
      padding-top: 70px;
    }
    .pl-10 {
      padding-left: 100px;
    }
    .pr-10 {
      padding-right: 100px;
    }
    form {
      .form-control {
        height: calc(2em + 1rem + 2px);
      }
      .form-group {
        margin-bottom: 1rem;
        &.btnSubmit {
          text-align: right;
        }
      }
    }
  }
  
  // Mobile
  @media (max-width: 575.98px) {
    .hero-static {
      .hero-container {
        .hero-title {
          margin-left: 9px;
          margin-right: 9px;
          display: none;
          .display-4 {
            font-size: 28px;
            line-height: 37px;
          }
          .summary {
            font-size: 14px;
            line-height: 25px;
          }
        }
      }
    }
    .features {
      p {
        font-size: 14px;
      }
    }
  }

  // Tablet Portrait
  @media (max-width: 768px) and (min-width: 576px) {
    // no custom css needed for tablet portrait
  }

  // Tablet Landscape & Small Resolution Desktop 
  @media (max-width: 1200px) and (min-width: 769px) {
    .hero-container {
      width: 80%;
    }
  }

  // Medium Resolution Desktop
  @media (min-width: 1201px) and (max-width: 1440px) {
    .image-group {transform: scale(.8)}
  }

}
