
.navbar-light-sticky {
    background: $white;
    padding: 0px;
    @include transition(padding .2s ease);
    .nav-item {
        position: relative;
    }
    .nav-item:hover .dropdown-menu {
        display: block;
    }
    #about-dropdown ~ .dropdown-menu {
        min-width: 25rem;
        right: 0;
        left: auto;
    }
    &.affix {
        @include box-shadow(-1px 3px 80px -9px rgb(134, 134, 134)); //example value
        .navbar-container {
            @include transition(padding .2s linear);
            padding-top: 0.2em !important;
            padding-bottom: 0.2em !important;
        }
        .top-nav,
        .search {
            display: none !important;
        }
        .navbar-brand {
            img {
                //@include transition(width .2s linear);
                width: 100px; //Adjust height
            }
        }
    }
    .navbar-brand {
        padding-bottom: 0.6875rem;
        margin-left: 2.375rem;
        margin-right: 2.125rem;
    }
    .dropdown {
        /*@include media-breakpoint-between(lg, xl) {
            &:hover {
                .dropdown-menu {
                    display: block;
                }
            }
        }*/
        ul {
            padding: 0px;
        }
    }
    .nav-link {
        font-size: 18px;
        font-weight: 500;
        line-height: 22px;
        color: $link-hover-color;
        @include media-breakpoint-between(lg, xl) {
            &:after {
                display: none;
            }
        }
    }
    .dropdown-nav {
        >li {
            >a {
                font-weight: 600;
                &:not(:hover) {
                    color: $link-color;
                }
            }
        }
    }
    .dropdown-subnav {
        a {
            color: $secondary;
            font-size: 15px;
        }
    }
    .temp-class {
        background: lighten($primary, 35%);
        min-height: 100rem;
        /* To show the scrollbar - It will be removed */
    }

    .navbar-container {
        padding-top: 1rem;
        padding-bottom: 1rem;
    }
    .main-nav {
        z-index: 2;
        .nav-link {
            padding-right: 25px;
            padding-left: 25px;
        }
    }
    .right-nav-cover {
        position: absolute;
        right: 7.9375rem;
        @media screen and (max-width: 1600px) {
            top: 6px;
            display: flex;
        }
    }
    .top-nav {
        li {
            font-size: 0;
        }
        .nav-link {
            display: inline-block;
            color: rgba($black, .6); //lighten
            font-size: 14px;
            font-weight: 500;
            line-height: 17px;
        }
        >li.nav-item {
            &:not(:last-child) {
                &:after {
                    display: inline-block;
                    content: "|";
                    font-size: 14px;
                    padding: 0 0.5rem;
                    color: rgba($black, .6); //lighten
                    text-align: center;
                    justify-content: center;
                    align-content: center;
                }
            }
        }
    }
    .bottom-nav {
        .nav-link {
            padding-bottom: 0.125rem;
            padding-right: 0.625rem;
            padding-left: 0.625rem;
        }
        .btn {
            &.btn-sucess {
                font-size: .88rem;
            }
            &.btn-outline-success {
                border: 2px solid $link-color;
                color: $link-color;
                &:hover {
                    background-color: $link-color;
                    border-color: $link-color;
                    color: $white;
                }
            }
        }
    }
    .btn-success {
        //font-size: 1.25rem;
        $padding-x: 1.25rem;
    }
    .language-dropdown {
        width: 91px;
        background: $light-white;
        color: $black;
        text-align: center;
        .dropdown-item,
        .nav-link {
            font-size: 14px;
            line-height: 17px;
            font-weight: 500;
            padding: 0px;
        }
        .dropdown-menu {
            padding: 24px 0px;
            text-align: center;
            min-width: 100% !important;
            @include border-top-radius(0);
            @include box-shadow(0 6px 12px rgba(0, 0, 0, .175));
            .dropdown-item {
                line-height: 24px;
            }
        }
        &:hover {
            width: 118px;
            background: $green;
            color: white;
            .dropdown-menu {
                display: block;
            }
            .nav-link {
                color: $white;
            }
            i {
                line-height: 1;
                font-size: 1rem;
                display: block;
            }
        }
    }

    .navbar-toggler {
        width: 37px;
        padding: 0px !important;
        outline: 0 !important;
        border: 0 !important;
        height: 37px;
        border-radius: 0px;
        span {
            cursor: pointer;
            border-radius: 1px;
            height: 2px;
            width: 24px;
            position: absolute;
            display: block;
            content: '';
            transition: all 0.4s ease-in-out;
            &:before,
            &:after {
                cursor: pointer;
                border-radius: 1px;
                height: 2px;
                width: 24px;
                position: absolute;
                display: block;
                content: '';
                transition: all 0.4s ease-in-out;
            }
        }
        &.collapsed span {
            background-color: $black;
        }
        span {
            &:before,
            &:after {
                background-color: $black;
            }
        }
        &.collapsed span {
            &:before,
            &:after {
                background-color: $black;
            }
        }
        span {
            background-color: transparent;
            &:before {
                transform: rotate(45deg);
                top: 0;
            }
            &:after {
                bottom: 0;
                transform: rotate(-45deg);
            }
        }
        &.collapsed span {
            &:before {
                transform: rotate(0deg);
                top: -10px;
            }
            &:after {
                bottom: -10px;
                transform: rotate(0deg);
            }
        }
    }
}


/* * * * * * * * * * * * * * */
/* Mobile Search Form */
/* * * * * * * * * * * * * * */
    .mobile-search-form {
        padding: 0 50px;
    
    .mobilenav-search-wrapper {
        position: relative;
        height: 44px;
        margin: 0 auto;
        padding: 0 25px;
        border-bottom: 1px solid #ccc;
        .mobilenav-search-input {
            -webkit-animation: none;
            animation: none;
            outline: none;
            border: none;
            background-color: transparent;
            width: 100%;
            line-height: 44px;
            padding: 0;
        }
        .mobilenav-search-submit {
            position: absolute;
            left: 0;
            top: 0;
            z-index: 1;
            height: 44px;
            width: 24px;
        }
        .mobilenav-search-reset {
            position: absolute;
            right: 0;
            top: 0;
            z-index: 1;
            height: 44px;
            width: 24px;
        }
        .mobilenav-search-reset,
        .mobilenav-search-submit {
            background: none;
            border: 0;
            padding: 0;
            box-sizing: content-box;
            color: inherit;
            cursor: pointer;
            font: inherit;
            line-height: inherit;
            overflow: visible;
            vertical-align: inherit;
        }
    }
}
@include media-breakpoint-down(sm) {
    .mobile-container {
        max-width: 720px;
        margin: auto;
    }
}
@include media-breakpoint-between(xs, lg) {
    .navbar-light-sticky {
        position: static;
        .main-nav {
            display: none;
        }
        .navbar-brand {
            margin-left: .5rem;
            margin-right: 0;    
        }
        .mobile-nav {
            .phone {
                font-size: 1rem;
            }
            .navbar-brand img {
                    width: 100px;
            }
            .navbar-container {
                padding-bottom: 0;
            }
            &.affix{
                .navbar-container {
                    padding-bottom: 0 !important;
                }
            }
            .nav-item {
                    border-bottom: 1px solid #efefef;
                    .nav-link {
                        padding: 0 10px;
                        line-height: 50px;
                    }
            }
            .dropdown-nav {
                list-style: none;
                padding-left: 20px;
                font-weight: 600;
                > li {
                    padding: 10px 0;
                    >a {
                        &:not(:hover) {
                            color: $black;
                        }
                    }
                    ul {
                        list-style: none;
                        padding-left: 10px; 
                        li {
                            line-height: 40px;
                        }
                    }
                }
            }
            .dropdown-menu {
                padding: 0px;
                border: none;
                .dropdown-row {
                    display: block;
                    .dropdown-subnav {
                        padding-left: 10px;
                    }
                }
            }
            .dropdown-toggle {
                &:after {
                    display: block;
                    border: none;
                    content: '\f077';
                    font-family: 'Font Awesome 5 Free';
                    margin-right: 15px;
                    float: right;
                    font-weight: 600;
                }
                &.collapsed {
                    &:after {
                        display: block;
                        border: none;
                        content: '\f078';
                        font-family: 'Font Awesome 5 Free';
                        margin-right: 15px;
                        float: right;
                        font-weight: 600;
                    }
                }
            }
        }
    }
}
@include media-breakpoint-down(lg) { 
    .mobile-container {
        max-width: 960px;
        margin: auto;
    }
    .mobile-container {
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        -webkit-box-pack: justify;
        -ms-flex-pack: justify;
        justify-content: space-between;
    }
}
@include media-breakpoint-between(lg, xl) {
    .dropdown-menu {
        &.spread-all {
            min-width: 54rem;
        }
    }
}
@include media-breakpoint-up(xl) {
    .sticky-nav-pad {
        padding-top: 7.938rem; 
    }
    .navbar-light-sticky {
        .dropdown-menu {
            padding: 1.5rem;
            margin: 0;
            border: none;
            .dropdown-row {
                display: flex;
                margin-left: $padding-width * -1;
                margin-right: $padding-width * -1;
                .sub-column {
                    display: block;
                    padding-left: $padding-width;
                    padding-right: $padding-width;
                    &+.sub-column {
                        border-left: 1px solid #D0D3D4;
                    }
                    .dropdown-nav {
                        padding: 0;
                        @include list-unstyled;
                        >li {
                            padding: 8px 0;
                        }
                    }
                    .dropdown-subnav {
                        padding: .5rem 0;
                        @include list-unstyled;
                    }
                    li a {
                        white-space: nowrap;
                    }
                }
            }
        }
    }
}
@media (min-width: 1200px) and (max-width: 1450px) {
    .navbar-light-sticky {
        &.affix {
            .main-nav {
                 .nav-link {
                    padding-right:20px;
                    padding-left: 20px;
                } 
            } 
            .bottom-nav {
                    padding-top: 12px;
            }
            .btn-success {
                @include small-btn-mixin();
            }
            .btn-outline-primary {
                @include small-btn-mixin();
            }
        } 
    }
}

@media (min-width: 1200px) and (max-width: 1300px) {
    .navbar-light-sticky {
        &.affix {
            .main-nav {
                 transform: translateX(-20px);
                .nav-link {
                    padding-right:13px;
                    padding-left: 13px;
                } 
            } 
            
        } 
    }
}


#searchModal {
    .searchbox {
        color: #000;
        pointer-events: auto;
        opacity: .9;
        padding: 8px 34px;
    }
    .search-submit {
        position: absolute;
        left: 0;
        top: 0;
        z-index: 1;
        height: 48px;
        width: 34px;
        pointer-events: auto;
        display: none;
    }
    .search-reset {
        position: absolute;
        right: 0;
        top: 0;
        z-index: 1;
        height: 48px;
        width: 24px;
        pointer-events: auto;
        display: none;
        &[disabled] {
            display: none;
        }
    }
}
