.why-five9 {
  h1 {
    font-size: 3.5rem !important;
  }
  h2,
  h3,
  h5 {
    font-family: "Silka";
    font-weight: 700;
    font-size: 40px;
    letter-spacing: -1.03px;
    line-height: 48px;
    color: $blue;
  }
  h2 {
    margin-bottom: 2.5rem;
  }
  h3 {
    color: $pink;
  }
  .angle-right {
    width: 1.5em;
    height: 1.5em;
    padding-bottom: 2px;
    fill: #00c268;
  }
  .control-buttons {
    a {
      margin: 0 10px;
    }
    a:active {
      rect {
        fill: #e0e0e0;
      }
    }
  }
  .carousel-indicators > li {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    display: inline-block;
    background-color: transparent;
    border: 3px solid #00C268;
    box-sizing: border-box;
    opacity: 1;
  }
  .carousel-indicators > li:not(.active) {
    background-color: #717171;
    border: none;
  }
  .carousel-inner {
    width: 90%;
    height: 380px;
  }
  .mobile-blockquote {
    .carousel-inner {
      width: 70%;
      height: 225px;
    }
  }

  .blockquote {
    &.pink-corner {
      position: relative;
      p {
        position: relative;
        z-index: 2;
        font-size: 1.1rem;
      }
      &:before {
        content: "";
        position: absolute;
        left: 0px;
        top: 0px;
        width: 4rem;
        height: 4rem;
        border-left: 13px solid #ff3465;
        border-top: 13px solid #ff3465;
      }
    }
    &.w-250 {
      padding: 2.5rem;
      max-width: 250px;
      @include media-breakpoint-down(lg) {
        max-width: 100%;
      }
    }
  }
  
  .article {
    .container {
      padding: 0px 4.75rem 4.75rem 4.75rem;
      @include media-breakpoint-down(lg) {
        padding: 2rem 3rem;
      }
    }
    .btn {
      margin-top: 1rem;
      margin-bottom: 1rem;
    }
    .cover-blockquotes {
      @include media-breakpoint-down(lg) {
        padding-top: 3rem;
      }
    }
  }

  .cover-quot {
    margin-top: 7rem;
    margin-bottom: 7rem;
    @include media-breakpoint-down(lg) {
      margin-top: 3rem;
      margin-bottom: 3rem;
    }
    .blockquote {
      position: relative;
      @include media-breakpoint-down(lg) {
        margin-top: 1rem !important;
      }
      p {
        font-weight: 700;
        font-size: 1.875rem;
        letter-spacing: -0.77px;
        line-height: 2.5rem;
        color: #150A1D;
        @include media-breakpoint-down(lg) {
          font-size: 1.5rem;
          line-height: 2rem;
        }
        @include media-breakpoint-down(md) {
          font-size: 1.875rem;
          line-height: 2.5rem;
        }
        @include media-breakpoint-down(sm) {
          font-size: 1.5rem;
          line-height: 2rem;
        }
        
      }
      .initial-quot {
        position: absolute;
        left: -18px;
        @include media-breakpoint-down(lg) {
          position: relative;
          left: 0;
        }
      }
      .blockquote-footer {
        color: #150A1D;
        padding-top: 0.5rem;
        &:before {
          display: none;
        }
      }
    }
  }

  .image-cards {
    background-color: #f7f7f7;
    .card {
      background-color: #f7f7f7;
      margin-bottom: 20px;
      position: relative;
      left: -25px;
    }
    .resource-title {
      font-size: 26px;
      line-height: 26px;
    }
    .resource-summary {
      font-size: 14px;
      line-height: 25px;
      padding-bottom: 20px;
    }
    .subtext {
      font-size: 14px;
      line-height: 25px;
    }
    .resource-card {
      border: none;
      padding: 40px 32px;
      .card-body {
        background-color: #fff;
        z-index: 2;
      }
    }
    .blue-bar:before {
      content: ' ';
      display: block;
      width: 32px;
      border-top: 5px solid #244CDE;
      margin: 30px 0 20px 0;
    }
    .card-body img {
      margin-top: -100px;
    }
    .card-body a {
      position: absolute;
      bottom: 60px;
      margin-top: 20px;
    }
    .inner-text {
      margin: 0 15px;
    }
    @include media-breakpoint-down(lg) {
      img {
        display: none;
      }
      .card {
        padding-top: 0;
        padding-bottom: 0;
        .card-body {
          width: 105vw;
          margin-left: -5vw;
          .inner-text {
            padding-left: 5vw;
            padding-top: 3vh;
          }
        }
      }
    }
  }

  // Mobile & Tablet-Portrait
  @media (max-width: 768px) {
    .btn {
      margin: 0 auto;
    }
  }

  // Tablet Landscape and Higher
  @media (min-width: 769px) {
    .why-five9 {
      padding: 25px;
    }
    .hero-half-img {
      transform: scale(.9);
    }
  }
  
  .video-btn {
    &.poster {
      display: inline-block;
      img {
        width: 100%;
      }
      &:hover {
        &:before {
          zoom: 1.1;
        }
      }
      &:before {
        content: "";
        position: absolute;
        width: 142px;
        height: 142px;
        background: url("/five9/assets/img/play-button.png");
        margin: auto;
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
      }
    }
  }
  
  
  
  @include media-breakpoint-down(lg) {
    .no-pm-lg {
      margin: 0px !important;
      padding: 0px !important;
    }
    .pm-15-lg {
      margin: 0px !important;
      padding-left: 15px !important;
      padding-right: 15px !important;
    }
    .pl-25-lg {
      padding-left: 25px !important;
      padding-right: 15px !important;
      margin: 20px 0px !important;
    }
  }
}