.pLanding {

  section {
    padding-top: 90px;
  }

  .left-content {
    padding-right: 30px;
  }

  .hero-static {
    overflow: hidden;
    .blueText {
      color: $blue;
    }
  }

  .innerCol{
    width: 100%;
    padding-bottom: 65px;
  }

  div.f9Reason {
    width: 30%;
    display: inline-block;
    padding-top: 20px;
    img {
      width: 20px;
      height: 16px;
    }
  }

  .plForm {
    padding-top: 70px;
    padding-bottom: 60px;
    position: relative;

    &.right-blue-overlay {
      height: 95%;
      &:before {
        background-color: $blue;
        content: "";
        position: absolute;
        left: 0px;
        top: 0px;
        width: 100vw;
        height: 100%;
        z-index: -1;
      }
    }

    div.productScreen {
      margin-left: -150px;
      margin-top: -150px;
    }

  }

  .innerColWhite {
    position: relative;
    width: 90%;
    padding-top: 40px;
    padding-left: 10%;
    margin-left: -10%;

    img{
      width: 70%;
      text-align: center;
      margin: 0 15%;
    }

    &:before {
      background-color: $white;
      content: "";
      position: absolute;
      left: 0px;
      top: 0px;
      width: 100vw;
      height: 100%;
      z-index: -1;
    }
    .content {
      position: relative;
      z-index: 1;
      padding-left: 10%;
    }
  }

  .btn-success {
    border-radius: 1.75rem;
  }
  .features {
    .feature-item {
      padding: 0 70px;
      margin-bottom: 50px;
      img {
        margin-left: -70px;
        margin-top: 5px;
        position: absolute;
      }
    }
  }

  .award-image-container {
    background-color: #fff;
    width: 100%;
    padding-top: 100%;
    position: relative; 
  }

  .award-image {
    position: absolute;
    transform: translateY(-50%);
    width: 100%;
    top: 50%;
    left: 0;
    right: 0;
  }

  .whyFive9 .container {
    background-color: $white;
    border-left: 13px solid $purple;
    border-bottom: 13px solid $purple;
    border-right: 13px solid $purple;
    padding: 5vw;
    h2 {
      color: $blue;
      font-size: 40px;
    }
    .col-md-4 {
      padding-left: 2%;
      padding-right: 3%;
    }
  }

  .desktopPlus {
    .container {
      padding-top: 4vh;
      padding-bottom: 4vh;
      color: $white;
      .leftText {
        vertical-align: center;
      }
      .adpContent {
        background-color: $purple;
        text-align: center;
        width: 100%;
        img {
          position: relative;
          top: -55px;
          width:90%;
          margin:auto;
        }
        .adpMid {
          text-align: center;
          .btn-success {
            position: relative;
            bottom: -25px;
          }
        }
      }
      .rightText {
        .dpRightUp {
          padding-top:20px;
        }
        .dpRightLow {
          padding-top:20px;
        }
      }
    }
  }

  .f9CTAs {
    .f9CTA {
      width: 80%;
    }
  }

  .f9Reasons2 {
    .rightHead{
      width: 100%;
      img {
        text-align: right;
        position: relative;
        left: 70%;
        margin-top: -50px;
        margin-bottom: -80px;
      }
    }
    .whiteBG {
      background-color: $white;
      padding: 12vh;
      .f9r2ColContent {
        font-size: 16px;
        line-height: 24px;
        width: 90%;
      }
    }
  }

  .ccaas {
    margin-top: 50px;
    margin-bottom: 40px;
    .hero-title {
      width: 80%;
      h2 {
        color: $pink;
      }
      p {
        padding-top: 1%;
        font-size: 22px;
      }
    }
    .blueVideo {
      img {
        width: 100%;
      }
    }
  }
}
