.offer-landing {

  .hero-static {
    padding-top: 20px;
    overflow: hidden;
  }

  .main-content {
    margin-bottom: 40px;
  }

  .hero-title {
    width: 90%;
    .display-4 {
      letter-spacing: -1.85px;
    }
  }

  .innerCol {
    position: relative;
    float: left;
    padding-right: 0px;
    padding-left: 0px;
    padding-top: 18px;
    &.left {
      width: 46%;
      padding-right: 6%;
      .disclaimerText {
        display: block;
      }
    }
    &.right{
      width: 50%;
      .disclaimerText2 {
        display: none;
      }
    }
  }

  @media(max-width: 660px){
    .innerCol.right, .innerCol.left {
      width: 100%;
    }
    .disclaimerText {
      display: none !important;
    }
    .disclaimerText2 {
      display: block !important;
    }
  }

  .gmqReason {
    font-size: 16px;
    text-indent: -41px;
    padding-left: 41px;
  }

  img.checkmark {
    height: 20px;
    width: 26px;
    margin-right: 4px;
  }

  .right-blue-overlay {
    padding-top: 70px;
    padding-bottom: 60px;
    position: relative;

    &:before {
      background-color: $blue;
      content: "";
      position: absolute;
      left: 0px;
      top: 0px;
      width: 100vw;
      height: 100%;
      z-index: -1;
    }
  }

  .disclaimerText {
    color: #717171;
    font-family: Silka;
    font-size: 9px;
    font-weight: 500;
    line-height: 15px;
  }

  .offer-preview-container {
    background-size: cover;
    width: 65%;
    height: 450px;
    box-shadow: 0px 0px 0px 3px #036caf inset;
    margin-left: 50px;
  }
  @media (max-width: 420px) {
    .display-4 {
      font-size: 43px;
      line-height: 55px;
    }
    .offer-preview-container {
      display: none;
    }
  }
  @media (max-width: 768px) {
    .offer-preview-container {
      height: 280px;
      width: 95%;
      margin-left: 10px;
    }
  }
  @media (max-width: 1024px) {
    .offer-preview-container {
      height: 350px;
    }
  }
}
