
footer {
  padding: 100px 0 20px;
  background: #fff;
  
  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    margin-bottom: 5px;
  }

  // .social-links {
  //   margin-top: 50px;
  //   li {
  //     margin-right: 5px;
  //   }
  // }

  a {
    color: $primary;
    &:hover {
      color: $primary;
    }
  }

  .active-language {
    cursor: pointer;
  }
  .dropdown-menu.languages {
    position: absolute;
    transform: translate3d(0px, -100%, 0px);
    top: 0px;
    left: auto;
    right: 0px;
    margin-bottom: 0;
  }

  .section-title {
    font-weight: bold;
    font-size: 19px;
  }

  .copyright {
    font-size: 14px;
  }

  .globe {
    height: 18px;
    vertical-align: text-top;
  }
}