.partners-new {
    .pull-left {
      float: left!important;
    }

    .pull-right {
      float: right!important;
      &.phone {
        margin-left: auto;
      }
    }

    .angle-right {
      width: 1.5em;
      height: 1.5em;
      padding-bottom: 2px;
      fill: #00c268;
    }

    header.sem-header .logo-container {
      padding: 20px 0;
      .logo {
          height: auto;
          width: 100px;
          margin-bottom: 5px;
          padding-right: 20px; 
          border-right: 1px solid gray;
      }
    }

    header.sem-header .contact-container {
      margin-top: 40px;
      .phone-sprite {
        display: inline-block;
        position: relative;
        height: 18px;
        width: 18px;
        top: 3px;
        margin-right: 3px;
        background-image: url(/assets/img/phone-sprite.png);
        background-repeat: no-repeat;
        -o-background-repeat: no-repeat;
        -moz-background-repeat: no-repeat;
        -webkit-background-repeat: no-repeat;
        background-position: 0 0;
        -o-background-position: 0 0;
        -moz-background-position: 0 0;
        -webkit-background-position: 0 0;
      }
    }

    header.sem-header .contact-container:active .phone-sprite, header.sem-header .contact-container:focus .phone-sprite, header.sem-header .contact-container:hover .phone-sprite {
      background-position: 0 100%;
      -o-background-position: 0 100%;
      -moz-background-position: 0 100%;
      -webkit-background-position: 0 100%;
    }

    .header-phone:active, .header-phone:focus, .header-phone:hover {
      text-decoration: none;
    }

    .header-phone {
      font-size: 24px;
    }

    .navbar-brand {
      display: inline-block;
      padding-top: 0.296875rem;
      padding-bottom: 0.296875rem;
      margin-right: 1rem;
      font-size: 1.25rem;
      line-height: inherit;
      white-space: nowrap;
    }

    section {
      padding: 0 5%;
      margin-top: 3% !important;
    }

    .left {
      text-align: left;
    }

    .right {
      text-align: right;
    }

    .ml-3{
      margin-top: 25px;
    }
    .col-md-7.left-content{
      padding-left: 0px;
      padding-right: 30px;
    }

    .hero-static {
      padding-top: 20px;
      overflow: hidden;
      .blueText {
        color: $blue;
      }
      .hero-title {
        width: 80%;
      }
    }

    .video-container {
      filter: grayscale(.2);
    }
    .video-container:hover {
      filter: grayscale(0);
    }

    .innerCol{
      width: 100%;
      padding-bottom: 65px;
    }

    div.f9Reason {
      display: inline-block;
      padding-top: 20px;
      img {
        width: 20px;
        height: 16px;
      }
    }

    .plForm {
      margin-top: 50px;
      div.productScreen {
        position: relative;
        left:-150px;
        top: -150px;
        width:120%;
        @media (max-width: 768px) {
          left: 0px;
          margin-top: 50px;
        }
      }
      padding-top: 70px;
      padding-bottom: 60px;
      position: relative;
      &.right-blue-overlay {
        height: 95%;
        &:before {
          background-color: $blue;
          content: "";
          position: absolute;
          left: 0px;
          top: 0px;
          width: 100vw;
          height: 100%;
          z-index: 0;
          @media (max-width: 768px) {
            left: -60px;
            width: 120vw;
          }
        }
      }
      form {
        position: relative;
        z-index: 1;
        width: 90%;
        padding-left: 10%;
        margin-top: -140px;
        .form-control {
          height: calc(2em + 1rem + 2px);
        }
        .form-group {
          margin-bottom: 1rem;
          &.btnSubmit {
            text-align: center;
          }
          input, textarea, select {
            -webkit-transition: all 0.30s ease-in-out;
            -moz-transition: all 0.30s ease-in-out;
            -ms-transition: all 0.30s ease-in-out;
            -o-transition: all 0.30s ease-in-out;
          }
          input:not([type=submit]):focus, textarea:focus, select:focus {
            box-shadow: 0 0 5px rgba(255, 174, 3, 1);
            border: 3px solid rgba(255, 174, 3, 1);
            // border-bottom: 4px solid rgba(255, 174, 3, 1);
          }
        }
        .btn-success {
          border-radius: 1.75rem;
        }
        .px-4 {
          padding-left: 2.5rem !important;
          padding-right: 2.5rem !important;
        }
        .py-2 {
          padding-bottom: 1rem !important;
          padding-top: 1rem !important;
        }
      }
      @media (max-width: 576px) {
        margin-top: 0px;
      }
    }

    .formText {
      color: $white;
      font-size: 22px;
      &.upper2 {
        font-size: 16px;
        line-height: 24px;
        font-family: Silka;
      }
      &.lower {
        font-size: 12px;
        line-height: 16px;
        a {color:$orange;}
        a:hover {text-decoration: underline;}
      }
    }

    .innerColWhite {
      position: relative;
      clear: both;
      width: 85%;
      padding-top: 40px;
      background-color: $white;
      img{
        width: 70%;
        text-align: center;
        margin: 0 15%;
      }

      &:before {
        background-color: $white;
        content: "";
        position: absolute;
        left: 0px;
        top: 0px;
        width: 100vw;
        height: 100%;
        z-index: 0;
      }
      .content {
        position: relative;
        z-index: 1;
        padding-left: 10%;
      }
    }
    @media (max-width: 576px) {
      .innerColWhite {
        width: 100%;
        margin-left: -33px;
        h1 {
          font-size: 2rem;
          text-align: center;
          position: relative;
          left: 15px;
        }
      }
    }

    .btn-success {
      border-radius: 1.75rem;
    }
    .features {
      .feature-item {
        padding: 0 70px;
        margin-bottom: 50px;
        img {
          margin-left: -70px;
          margin-top: 5px;
          position: absolute;
        }
      }
    }
    @media (min-width: 768px) and (max-width: 1200px) {
      .imgOverflow {
        position: absolute;
        left: -40px;
      }
      .feature-item {
        h3 {
          font-size: 1.5rem;
        }
      }
    }

    .award-image {
      max-width: 100%;
    }

    .whyFive9 .container {
      background-color: $white;
      border-left: 13px solid $purple;
      border-bottom: 13px solid $purple;
      border-right: 13px solid $purple;
      padding: 5vw;
      h2 {
        color: $blue;
        font-size: 40px;
      }
      .col-md-4 {
        padding-left: 2%;
        padding-right: 3%;
      }
      // @media (max-width: 1200px) {
        border-top: 13px solid $purple;
      // }
    }

    .desktopPlus {
      .container {
        padding-top: 4vh;
        padding-bottom: 4vh;
        color: $white;
        .leftText {
          vertical-align: center;
        }
        .adpContent {
          background-color: $purple;
          text-align: center;
          width: 100%;
          img {
            position: relative;
            top: -55px;
            width:90%;
            margin:auto;
          }
          .adpMid {
            text-align: center;
            .btn-success {
              position: relative;
              bottom: -25px;
            }
          }
        }
        .rightText {
          .dpRightUp {
            padding-top:20px;
          }
          .dpRightLow {
            padding-top:20px;
          }
        }
      }
    }

    .f9CTAs {
      .f9CTA {
        width: 80%;
      }
      @media (max-width: 576px) {
        .f9CTA {
          width: 100%;
          h2 {
            font-size: 1.5rem;
          }
        }
      }
    }

    .f9Reasons2 {
      .rightHead{
        width: 100%;
        img {
          text-align: right;
          position: relative;
          left: 70%;
          margin-top: -50px;
          margin-bottom: -80px;
        }
      }
      .whiteBG {
        background-color: $white;
        padding: 12vh;
        .f9r2ColContent {
          font-size: 16px;
          line-height: 24px;
          width: 90%;
        }
      }
      @media (max-width: 768px) {
        .whiteBG {
          padding: 45px;
          .col-md-3 {
            margin-bottom: 35px;
          }
        }
      }
      @media (max-width: 1200px) {
        .rightHead {
          img {
            left: 60%;
          }
        }
      }
    }

    .innerCol {
      position: relative;
      float: left;
      padding-right: 0px;
      padding-left: 0px;
      padding-top: 18px;
      &.left {
        width: 46%;
        padding-right: 6%;
      }
      &.right{
        width: 46%;
      }
      .f9Client {
        width:48%;
      }
    }
    @media (max-width: 576px) {
      .innerCol {
        transform: scale(.9)
      }
    }

    .ccaas {
      margin-top: 50px;
      margin-bottom: 40px;
      .hero-title {
        width: 80%;
        h2 {
          color: $pink;
        }
        p {
          padding-top: 1%;
          font-size: 22px;
        }
      }
      .blueVideo {
        img {
          width: 100%;
        }
      }
    }

  @media (max-width: 576px) {
    .hero-static {
      .hero-title {
        .display-4 {
          font-size: 2.5rem;
        }
      }
    }
  }
  @media (max-width: 768px) {
    .hero-static { 
      .hero-title {
        width: 100%;
      }
      .innerColWhite:before {
        width: 120vw;
        left: -60px;
      }
    }
  }
}
