.guided-tour {
  .cards {

    .card-cover {
      background-color: #244cde;
      border-radius: 0;
      padding: 0 30px;
      padding-bottom: 1rem;
      position: relative;

      @include media-breakpoint-down(md) {
        width: 100% !important;
        margin: 0px !important;
        flex: none;
      }

      @include transition(all .2s ease);

      &:hover {

        .card-top {
          margin-bottom: 1rem;
          // &:before {
          //   display: none;
          // }
        }

        .card-top-img {
          display: none;
        }

        .card-top-text {
          display: block;
          margin-bottom: 0rem;
          color: $white;
        }

        .card-title {
          padding-top: 2rem;
          color: $green;
          border-color: $green;
          margin-bottom: 0rem;
        }

      }

    }

    .card-top {
      margin-bottom: 2rem;
      margin-bottom: 2rem;

      &:before {
        left: 0px;
        display: block;
        content: '';
        position: absolute;
        width: 100%;
        height: 4rem;
        background-color: #f7f7f7;
        z-index: 0;
      }

      .card-top-text {
        position: relative;
        display: none;
        padding-top: 6rem;
        margin-bottom: 2rem;
        z-index: 1;
        @include transition(all .2s ease);
      }

      .card-top-img {
        @include box-shadow(0px 2px 4px 0px rgba(0, 0, 0, 0.50));
        width: 100%;
        position: relative;
        z-index: 1;
        @include transition(all .2s ease);
      }
    }

    .card-title {
      border-left: 2px solid #fff;
      padding-left: 20px;
      margin-left: 10px;
      color: $white;
      cursor: pointer;
      @include transition(all .2s ease);
    }
  }

  .no-bg {
    background: none !important;
  }
}