@import url('https://use.fontawesome.com/releases/v5.8.1/css/all.css');
@font-face {
    font-family: 'Silka';
    font-display: fallback;
    font-style: normal;
    font-weight: 400;
    src: local('Silka Regular'), local('Silka-Regular'), url('../fonts/Silka-Regular.otf') format('opentype');
}

@font-face {
    font-family: 'Silka';
    font-display: fallback;
    font-style: normal;
    font-weight: 500;
    src: local('Silka Medium'), local('Silka-Medium'), url('../fonts/Silka-Medium.otf') format('opentype');
}

@font-face {
    font-family: 'Silka';
    font-display: fallback;
    font-style: normal;
    font-weight: 700;
    src: local('Silka Bold'), local('Silka-Bold'), url('../fonts/Silka-Bold.otf') format('opentype');
}

@font-face {
    font-family: 'Silka';
    font-display: fallback;
    font-style: normal;
    font-weight: 300;
    src: local('Silka Light'), local('Silka-Light'), url('../fonts/Silka-Light.otf') format('opentype');
}