.help-block { color: $red; }

.mktoForm {
  width: 100%!important;

  .mktoField:not([type="checkbox"])  {
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: 0;
    color: #495057;
    font-size: 1rem;
    font-weight: 400;
    height: calc(2em + 1rem + 2px);
    line-height: 1.625;
    padding: .5rem .75rem;
    width: 100%!important;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
  }
  .mktoFieldDescriptor {
    margin-bottom: 15px!important;
    position: relative;
  }
  .mktoLabel,
  .mktoAsterix,
  .mktoErrorDetail {
    display: none;
  }

  .mktoLabel[for="Consent_to_Processing__c"] {
    display: block;
    color: #fff;
    cursor: pointer;
    float: right;
    width: calc(100% - 20px)!important;
    font-size: 16px;
    font-weight: 400;
    margin-bottom: 35px;

    a {
      color: #fff;
    }
  }
  .mktoButtonWrap {
    margin: 0!important;
  }

  button[type="submit"] {
    max-width: 300px;
    margin: 35px 0 20px;
    background-color: $green;
    border: 2px solid $green;
    display: block;
    padding: .75rem 3rem;
    font-size: 16px;
    text-decoration: none;
    text-align: center;
    letter-spacing: 0.5px;
    color: #fff;
    border-radius: 100px;
    cursor: pointer;
    user-select: none;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;

    &:hover {
      background-color: darken($green, 7.5%);
      border-color: darken($green, 10%);
    }
  }

  .mktoError {
    position: absolute;
    z-index: 99;
    right: auto!important;
    bottom: auto!important;
  }
  .mktoError .mktoErrorArrow {
    background-color: #a94442;
    border: 1px solid #9f1300;
    border-right: none;
    border-bottom: none;
    display: inline-block;
    height: 16px;
    -webkit-transform: rotate(45deg);
    -moz-transform: rotate(45deg);
    transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    width: 16px;
    margin-top: 5px;
  }
  .mktoError .mktoErrorArrowWrap {
    width: 16px;
    height: 8px;
    overflow: hidden;
    position: absolute;
    top: 0;
    left: 5px;
    z-index: 100;
  }
  .mktoError .mktoErrorMsg {
    background-color: #a94442;
    padding: 2px 5px;
    color: #fff;
    margin-top: 7px;
  }
  .mktoError .mktoErrorMsg .mktoErrorDetail {
    display: block;
  }

}