.breadcrumb-item {
    font-size: 14px;
    font-weight: 500;
    letter-spacing: 0;
    a {
        color: $light-black;
    }
}

.hero {
    position: relative;
    background: $light-white;
    &.blue-right-frame {
        background: $blue;
        &:before {
            content: "";
            position: absolute;
            background: $light-white;
            height: 100%;
            top: 0;
            z-index: 0;
            /* %70 ( x / 100 * 75) */
            @include media-breakpoint-up(lg) {
                width: calc(((100% - #{$container-width}) / 2) + ((#{$container-width} / 100) * 70));
            }
            @include media-breakpoint-between(md, lg) {
                width: calc(((100% - 960px) / 2) + ((960px / 100) * 70));
            }
            @include media-breakpoint-down(md) {
                width: 100%;
            }
        }
        &.white-bottom-line {
            &:after {
                content: "";
                position: absolute;
                bottom: 0;
                height: 100px;
                z-index: 1;
                background: white;
                margin-left: auto;
                margin-right: auto;
                left: 0;
                right: 0;
                @include media-breakpoint-between(lg, xl) {
                    width: $container-width;
                }
                @include media-breakpoint-down(lg) {
                    display: none;
                }
            }
        }
    }
    .hero-half-img {
        img {
            position: relative;
            z-index: 2;
            width: 100%;
        }
    }
    h1.hero-title {
        font-family: "Silka";
        font-weight: 700;
        font-size: 72px;
        letter-spacing: -1.85px;
        line-height: 72px;
        color: $blue;
        @include media-breakpoint-down(lg) {
            padding-top: 1rem;
        }
    }
    .hero-text {
        font-size: 1.25rem;
        color: $black;
        line-height: 1.875rem;
    }
}