.partners-wins {
  .btn {
    white-space: nowrap;
    font-size: 18px;
  }
  .control-buttons {
    display: none;
    margin-left: 20px;
    margin-right: 21px;
    a {
      margin-right: 20px;
    }
    a:active {
      rect {
        fill: #e0e0e0;
      }
    }
  }
  .angle-right {
    width: 1.5em;
    height: 1.5em;
    padding-bottom: 2px;
    fill: #00c268;
    display: inline-block;
  }

  .hero-static {
    position: relative;
    overflow: hidden;
    background-color: #fff;
    .hero-container {
      .hero-image-tablet {
        display: none;
      }
      .hero-title-mobile {
        display: none;
        margin-left: 9px;
        margin-right: 9px;
        .hero-image-mobile {
          margin-top: -25px;
          margin-bottom: 16px;
        }
      }
      .hero-title {
        width: 637px;
        .display-4 {
          font-size: 60px;
          line-height: 66px;
        }
        .breadcrumb-item {
          font-size: 18px;
          line-height: 22px;
          font-weight: 200;
          color: #727272;
        }
        .breadcrumb-underline {
          border-bottom: 1px solid rgba(99, 99, 99, 0.3);
        }
        .summary {
          font-size: 20px;
          line-height: 34px;
          width: 90%;
        }
        .hero-image {
          margin-top: 50px;
        }
      }
    }
  }

  .customer-logos {
    background-color: #fff;
    .customer-logos-mobile,
    .customer-logos-tablet {
      display: none;
    }
    .customer-title {
      font-size: 22px;
      line-height: 26px;
    }
    .btn {
      margin-top: 55px;
    }
  }


  .panel-library {
    background-color: #fff;
    .panel-title {
      font-size: 28px;
      line-height: 37px;
    }
    .panel-summary {
      font-size: 14px;
      line-height: 25px;
    }
    .subtext {
      font-size: 16px;
      line-height: 25px;
    }
    .panel-card {
      border: none;
      padding: 40px 80px;
      .card-body {
        background-color: #f7f7f7;
      }
    }
    #mobile-panels {
      display: none;
    }

    .customer-acquisition {
      font-size: 22px;
      line-height: 26px;
    }
  }
  

  // Mobile
  @media (max-width: 575.98px) {
    .hero-static {
      .hero-container {
        .hero-title-mobile {
          display: block;
        }
        .hero-title {
          margin-top: 19px;
          margin-left: 9px;
          margin-right: 9px;
          display: none;
          .display-4 {
            font-size: 28px;
            line-height: 37px;
          }
          nav {
            display: none;
          }
          .summary {
            font-size: 16px;
            line-height: 25px;
            width: 100%;
          }
        }
        .trophy-group {
          display: none;
        }
      }
    }

    .customer-logos {
      .customer-logos-desktop,
      .customer-logos-tablet {
        display: none;
      }
      .customer-logos-mobile {
        display: block;
        margin-top: -40px;
      }
      .customer-title {
        font-size: 16px;
        line-height: 25px;
      }
      .customer-logos {
        background-color: #f7f7f7;
      }
    }

    .panel-library {
      h1 {
        font-size: 24px;
        line-height: 32px;
        padding-bottom: 20px;
        margin-top: -50px;
      }
      .card-body {
        margin: 0 -40px 0 -40px;
        img:not(#doordash-logo) {
          height: 40px;
        }
        #doordash-logo {
          width: 196px;
          height: auto;
        }
        a {
          font-size: 16px;
          line-height: 25px;
        }
      }
      .panel-card {
        padding: 5px 20px;
      }
    }

    .customer-acquisition {
      p {
        font-size: 16px;
        line-height: 25px;
      }
      .btn {
        font-size: 16px;
      }
    }
  }

  // Tablet Portrait
  @media (max-width: 768px) and (min-width: 576px) {
    .hero-static {
      .hero-container {
        .hero-image-tablet {
          display: block;
        }
        .hero-title {
          margin: 0 auto;
        }
        .trophy-group {
          display: none;
        }
      }
    }

    .customer-logos {
      .customer-logos-desktop,
      .customer-logos-mobile {
        display: none;
      }
      .customer-logos-tablet {
        display: block;
      }
    }

    .panel-library {
      h1 {
        font-size: 24px;
        line-height: 32px;
        padding-bottom: 20px;
        margin-top: -50px;
      }
      .card-body {
        margin: 0 -40px 0 -40px;
        img:not(#doordash-logo) {
          height: 40px;
        }
        #doordash-logo {
          width: 280px;
          height: auto;
        }
        a {
          font-size: 16px;
          line-height: 25px;
        }
      }
      .card-body *:not(svg) {
        padding: 0 40px;
      }
      .btn {
        margin-top: 30px;
      }
      .panel-card {
        padding: 5px 20px;
      }
    }
  }

  // Tablet Landscape & Small Resolution Desktop 
  @media (max-width: 1200px) and (min-width: 769px) {
    .hero-static {
      .hero-container {
        .hero-image-tablet {
          display: block;
        }
        .hero-title {
          margin: 0 auto;
        }
        .trophy-group {
          display: none;
        }
      }
    }

    .customer-logos {
      .customer-logos-desktop img {
        width: 775px;
      }
    }

    .panel-library {
      h1 {
        font-size: 24px;
        line-height: 32px;
        padding-bottom: 20px;
        margin-top: -50px;
      }
      .card-body {
        margin: 0 -40px 0 -40px;
        img:not(#doordash-logo) {
          height: 40px;
        }
        #doordash-logo {
          width: 280px;
          height: auto;
        }
        a {
          font-size: 16px;
          line-height: 25px;
        }
      }
      .card-body *:not(svg) {
        padding: 0 40px;
      }
      .btn {
        margin-top: 30px;
      }
      .panel-card {
        padding: 5px 20px;
      }
    }
  }

  // Medium Resolution Desktop
  @media (min-width: 1201px) and (max-width: 1440px) {
    .trophy-group {transform: scale(.8)}
  }
  
}

