.partners-acquisitions {
  .btn {
    white-space: nowrap;
    font-size: 18px;
  }
  .angle-right {
    width: 1.5em;
    height: 1.5em;
    padding-bottom: 2px;
    fill: #00c268;
  }

  .hero-static {
    position: relative;
    overflow: hidden;
    background-color: #fff;
    .hero-container {
      .hero-image-tablet {
        display: none;
      }
      .hero-title-mobile {
        display: none;
        margin-left: 9px;
        margin-right: 9px;
        .hero-image-mobile {
          margin-top: -25px;
          margin-bottom: 16px;
        }
      }
      .hero-title {
        width: 637px;
        .display-4 {
          font-size: 60px;
          line-height: 66px;
        }
        .breadcrumb-item {
          font-size: 18px;
          line-height: 32px;
          font-weight: 200;
          color: #727272;
        }
        .breadcrumb-underline {
          border-bottom: 1px solid rgba(99, 99, 99, 0.3);
        }
        .summary {
          font-size: 20px;
          line-height: 34px;
          width: 90%;
        }
        .hero-image {
          margin-top: 50px;
        }
      }
    }
  }

  .panel-library {
    background-color: #fff;
    .panel-title {
      font-size: 28px;
      line-height: 37px;
    }
    .panel-summary {
      font-size: 14px;
      line-height: 25px;
    }
    .subtext {
      font-size: 16px;
      line-height: 25px;
    }
    .panel-card {
      border: none;
      padding: 40px;
      .card-body {
        background-color: #f7f7f7;
        width: 100%;
      }
    }
    #mobile-panels {
      display: none;
    }

    .customer-acquisition {
      font-size: 22px;
      line-height: 26px;
    }
  }
  

  // Mobile
  @media (max-width: 575.98px) {
    .hero-static {
      .hero-container {
        .hero-title-mobile {
          display: block;
        }
        .hero-title {
          margin-top: 19px;
          margin-left: 9px;
          margin-right: 9px;
          display: none;
          .display-4 {
            font-size: 28px;
            line-height: 37px;
          }
          nav {
            display: none;
          }
          .summary {
            font-size: 16px;
            line-height: 25px;
            width: 100%;
          }
        }
        .trophy-group {
          display: none;
        }
      }
    }

    .panel-library {
      h1 {
        font-size: 24px;
        line-height: 32px;
        padding-bottom: 20px;
        margin-top: -50px;
      }
      .panel-card {
        border: none;
        padding: 5px 0;
        .card-body {
          background-color: #f7f7f7;
          width: 120%;
          margin: 0 -40px 0 -20px;
          img {
            height: auto;
            width: 150px;
          }
          a {
            font-size: 16px;
            line-height: 25px;
          }
        }
      }
    }

    .customer-acquisition {
      p {
        width: 80%;
        font-size: 16px;
        line-height: 25px;
      }
      .btn {
        font-size: 16px;
      }
    }
  }
  

  // Tablet Portrait
  @media (max-width: 768px) and (min-width: 576px) {
    .hero-static {
      .hero-container {
        .hero-image-tablet {
          display: block;
        }
        .hero-title {
          margin: 0 auto;
        }
        .trophy-group {
          display: none;
        }
      }
    }

    .customer-logos {
      .customer-logos-desktop {
        display: none;
      }
      .customer-logos-mobile {
        display: block;
      }
    }

    .panel-library {
      h1 {
        font-size: 24px;
        line-height: 32px;
        padding-bottom: 20px;
        margin-top: -50px;
      }
      .panel-card {
        border: none;
        padding: 5px 0;
        .card-body {
          background-color: #f7f7f7;
          width: 120%;
          margin: 0 -40px 0 0px;
          img {
            height: auto;
            width: 200px;
          }
          a {
            font-size: 16px;
            line-height: 25px;
          }
        }
      }
    }
  }

  // Tablet Landscape & Small Resolution Desktop 
  @media (max-width: 1200px) and (min-width: 769px) {
    .hero-static {
      .hero-container {
        .hero-image-tablet {
          display: block;
        }
        .hero-title {
          margin: 0 auto;
        }
        .trophy-group {
          display: none;
        }
      }
    }

    .customer-logos {
      .customer-logos-desktop img {
        width: 775px;
      }
    }

    .panel-library {
      h1 {
        font-size: 24px;
        line-height: 32px;
        padding-bottom: 20px;
        margin-top: -50px;
      }
      .panel-card {
        border: none;
        padding: 5px 0;
        .card-body {
          background-color: #f7f7f7;
          width: 120%;
          margin: 0 0 0 0px;
          img {
            height: auto;
            width: 150px;
          }
          a {
            font-size: 16px;
            line-height: 25px;
          }
        }
      }
    }
  }

  // Medium Resolution Desktop
  @media (min-width: 1201px) and (max-width: 1440px) {
    .trophy-group {
      transform: scale(.8);
      svg {
        margin-top: -50px;
      }
    }
  }
  
}  
