
.homepage {
  body {
    background-color: #f2f2f2;
  }
  .btn {
    white-space: nowrap;
  }
  .hero-static {
    position: relative;
    overflow: hidden;
    background-color: #fff;
    h1 {
      line-height: 55px;
    }
  }
  .hero-title {
    width: 90%;
    .display-4 {
      font-size: 4.75rem;
      line-height: 80px;
    }
  }
  .hero-image {
    margin-top: 50px;
    .bg {
      background-color: $primary;
      left: 0;
      top: 0;
      bottom: 0;
      position: absolute;
      width: 50vw;
      z-index: 0;
    }
    img {
      margin-top: -40px;
      margin-left: -60px;
      z-index: 1;
      position: relative;
    }
  }

  .cta-sticky {
    position: absolute;
    background: #f2f2f2;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 1;
    flex-direction: column;
    justify-content: center;

    .cta {
      margin-bottom: 5px;
      width: 90px;
    }
    a {
      display: block;
      text-align: center;
      padding: 10px;
      font-size: 12px;
      color: $green;
      &:hover { color: $green; }
    }
    img {
      display: block;
      margin: auto;
      margin-bottom: 5px;
    }
  }

  .digital-transformation img {
    float: right;
  }
  .digital-transformation-headings {
    color: #fff;
    margin-right: 25%;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }

  .pillars {
     > .container {
      background-color: #fff;
      box-shadow: 0px 5px 80px #e5e5e5;
    }
    .pillar {
      padding: 40px;
    }
    .pillar:hover {
      background-color: $primary;
      color: #fff;
      margin-top: -25px;
      padding-top: 65px;
    }
    .pillar:hover .text-primary {
      color: #fff!important;
    }
    .pillar:hover img {
      display: none;
    }
    .pillar:hover img.hover {
      display: block;
    }
    .pillar img.hover {
      display: none;
    }
  }

  .analyst-reports {
    .container {
      padding-top: 25px;
      border-top: 1px solid $red;
    }
    .col-md-6 {
      padding: 25px 50px 50px;
    }
    .col-md-6:first-child {
      border-right: 1px solid $red;
    }
    .orange-bar:before {
      content: ' ';
      display: block;
      width: 38px;
      border-top: 5px solid orange;
      margin: 30px 0 20px 0;
    }
  }

  .spring-release {
    overflow: hidden;
    padding: 50px 0;

    .container {
      position: relative;
    }
    .bg {
      background-color: #ff3465;
      height: 1000px;
      left: 75%;
      position: absolute;
      top: -120px;
      width: 50vw;
    }
    strong {
      color: $primary;
    }
    ol {
      list-style: none;
      counter-reset: sr-feature-counter;
      padding-left: 72px;
      padding-bottom: 10px;
      padding-right: 20px;
      border-left: 3px solid #e5e5e5;
    }
    ol li {
      counter-increment: sr-feature-counter;
      margin-left: -3px;
    }
    ol li::before {
      content: "0" counter(sr-feature-counter);
      color: #bfbfbf;
      padding: 0px 25px 25px;
      font-weight: bold;
      margin-left: -72px;
      border-left: 3px solid #e5e5e5;
    }
    ol li.active::before {
      border-left: 3px solid $primary;
      color: $primary;
    }
    .sr-carousel {
      margin-top: 100px;
      box-shadow: 0px 5px 80px #00000030;
    }
    .carousel-control-prev, 
    .carousel-control-next {
      position: absolute;
      top: calc(50% - 30px);
      height: 60px;
      width: 60px;
      background: #ffba01;
      opacity: 1;
    }
    .carousel-control-prev-icon,
    .carousel-control-next-icon {
      width: 30px;
      height: 30px;
    }
    .carousel-control-prev {
      margin-left: -40px;
    }
    .carousel-control-next {
      margin-right: -40px;
    }
  }

  .trust {
    .container {
      position: relative;
    }
    .bg {
      background-color: #fff;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 90px;
      z-index: -1;
    }
    .bg-img {
      position: absolute;
      top: 50%;
      bottom: 0;
      right: 0;
      transform: translateY(-50%);
    }
    .trust-container {
      display: flex;
      flex-wrap: wrap;
      width: 90%;
      padding: 80px 100px 0;
    }
    .trust-item {
      border-left: 12px solid $yellow;
      width: 50%;
      padding-left: 40px;
      margin-bottom: 100px;

      h1 {
        font-weight: bold;
      }
      h1,h4 {
        color: $primary;
      }
    }
  }

  .features {
    .feature-item {
      margin-bottom: 25px;
      font-size: 22px;

      img {
        margin-right: 15px;
      }
    }
  }

  .resource-cards {
    .resource-card {
      border: none;
      background-color: none;
    }
    .card-body {
      background-color: $primary;
      border-radius: 0;
      color: #fff;
      padding: 0 3.5rem 3rem;
    }
    .card-title {
      border-left: 2px solid #fff;
      padding-left: 20px;
    }
    .card-img-top {
      height: 150px;
      border-radius: 0;
      margin: -60px 0 50px -1.75rem;
      width: calc(100% + 3.5rem);
      background-position: center;
          background-size: cover;
        background-repeat: no-repeat;
    }
  }

  .demo {
    background-color: $primary;
    margin-top: 150px;

    .demo-title {
      margin-top: 95px;
      margin-left: 40px;
      max-width: 75%;
    }
    .demo-bg {
      background: url('../img/leadform-bg.png');
      background-size: cover;
      background-position: center;
      color: #fff;
      margin-top: -80px;
      margin-bottom: 80px;
    }
    .demo-form {
      padding: 50px;
    }
  }
}